body {
    font-family: Arial, sans-serif;
  }

  .range-container {
    display: flex;
    align-items: center;
  }
  
  .label {
    flex: 0 0 auto;
  }
  
  .leftlabel {
    margin-right: 10px; /* Adjust as needed */
  }
  
  .rightlabel {
    margin-left: 10px; /* Adjust as needed */
  }
  
  .content-container {
    position: absolute;
    top: 33%;
    left: 0;
    right: 0;
  }
  
  .center-content {
    max-width: 800px; /* Adjust as needed */
    margin: 0 auto;
  }
    
  .container {
    max-width: 800px;
    margin: 0 auto;
  }
  
  h1 {
    text-align: center;
  }
  
  input[type="range"] {
    width: 100%;
  }
  
  button {
    display: block;
    width: 100%;
    padding: 10px 20px;
    margin: 10px auto;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .image-container {
    max-width: 1000px;
    max-height: 1000px;
    margin: 0 auto;
    text-align: center;
    display: flex;
    justify-content: center; /* Added */
  }
  
  .image-container img {
    max-width: 100%;
    max-height: 100%;
    margin: auto;
  }
  
  
  .content-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .center-content {
    max-width: 800px; /* Adjust as needed */
  }
  
